body {
  font-family: Arial, serif;
  font-weight: 400;
  font-size: 32px;
  line-height: 1.7;
  color: #4d4d4d;
  background: #fff;
}

h1 {
  margin: 0;
  font-size: 48px;
}

h2 {
  font-size: 32px;
}

h3 {
  margin: 0 0 20px;
  font-size: 16px;
}

/* Header コンポーネント */
.main-cover {
  background-color: transparent;
  background-size: cover;
  background-attachment: fixed;
  position: relative;
  height: 100vh;
  width: 100%;
}

.main-cover .overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.8);
}

.profile-thumb {
  background-size: cover !important;
  background-position: center center;
  background-repeat: no-repeat;
  position: relative;
  height: 200px;
  width: 200px;
  margin: 0 auto;
  margin-bottom: 30px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  border-radius: 50%;
}

.social-icons {
  margin: 0;
  padding: 0;
}

.social-icons li {
  margin: 0;
  padding: 0;
  list-style: none;
  display: inline-block;
}

.social-icons li a {
  display: inline-block;
  padding-left: 10px;
  padding-right: 10px;
}

.container {
  width: 1170px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.display-table {
  width: 100%;
  height: 100vh;
  display: table;
}

.display-table-contents {
  padding: 128px 0;
  text-align: center;
  display: table-cell;
  vertical-align: middle;
}

.title-text {
  position: relative;
}

.icon-link {
  position: relative;
}

/* Skill コンポーネント */
#skills {
  padding: 128px 0;
  clear: both;
}

.heading {
  margin-bottom: 32px;
  text-align: center;
}

.skills-container {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.skill-item {
  margin: 0 48px;
}

/* Contact コンポーネント */
#contact {
  padding: 128px 0;
  clear: both;
}

.contact {
  background: #000;
}

.contact-contents {
  color: white;
  text-align: center;
}

.description {
font  : 16px;
}

.btn {
  font-family: "Space Mono", Arial, serif;
  font-size: 16px;
  border: none !important;
  border-radius: 30px;
  padding: 16px 64px;
  text-decoration: none;
}

#contact .btn {
  height: 54px;
  background: #fff;
  color: #000;
}
